*{
  padding: 0;
  margin: 0;
}
.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  background-color: #ffc4c4;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  font-size: 1.2rem;
  background-color: rgb(255, 223, 227);
}
.App h1{
  color: olive;
  padding: 20px;
  /* margin-top: 10px; */
}
.top{
  width: 300px;
  display: flex;
}
.top input{
  font-size: 1.2rem;
  border: none;
  background-color: transparent;
  padding: 10px 15px;
  outline: none;
  color: olive;
  border: 1px solid olive;
}
.top button{
  border: none;
  width: 100%;
  background-color: olive;
  color: white;
  font-weight: 600;
  font-size: 1.1rem;
}
.bottom{
  width: 300px;
}
.bottom ul{
  list-style-type: none;
  margin-top: 20px;
}
.bottom li{
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  background-color: rgb(163, 163, 0);
  color: white;
  margin-top: 5px;
  /* padding: 10px; */
  text-transform: uppercase;cursor: pointer;
  transition: all 200ms ease-in-out;
}
.bottom li:hover{
  scale: 1.02;
  font-weight: 600;
  opacity: 0.9;

}
.bottom .left{
  display: flex;
  justify-content: flex-start;
  flex-grow: 2;
}
.bottom li label{
  padding: 5px 10px;
  flex-grow: 2;
  text-align: justify;
}
.bottom li input[type='checkbox']{
  margin-left: 5px;
  width: 20px;
}
.bottom li button{
  background-color: rgb(246, 88, 120);
  color: white;
  outline: none;
  border: none;
  padding: 5px 10px;
  font-size: 1.2rem;
  cursor: pointer;
}

.isDone{

}
.bottom li.gray{
  background-color: gray;
}
.bottom li.gray label {
  text-decoration: line-through;
}